    margin-right: 9px;
}
.services_discreption {
    height: 32rem;
    width: 100%;
    padding: 3.5rem 4rem 0 4rem;
    display: grid;
    grid-template-rows: auto 1fr;
    grid-row-gap: 33px;
    row-gap: 33px;
    position: relative;
}
.services_discreption::after {
    content: '';
    position: absolute;
    background-image: url(/assets/smallCarShadow.png);
    width: 250px;
    height: 103px;
    background-repeat: no-repeat;
    background-size: contain;
    top: 314px;
    right: 0;
    z-index: 1;
}
.services_discreption::before {
    content: '';
    position: absolute;
    background-image: url(/assets/bigCarShadow.png);
    width: 358px;
    height: 261px;
    background-repeat: no-repeat;
    background-size: contain;
    top: 35px;
    right: 21px;
    z-index: 1;
}
.offers_container {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: repeat(5, auto);
    grid-gap: 13px 32px;
    z-index: 2;
}
.offers_container p {
    color: white;
    line-height: 19px;
    font-size: 15.5px;
